<template>
    <div class="page">
                      <ul>
                          <li v-if="curPage > 1" @click="changePage(0)">上一页</li>
                          <li v-if="curPage >= 3" @click="changePage(1)">1</li>
                          <li v-if="curPage >= 3">...</li>
                          <li v-for="item in 5" :key="item" 
                              v-if="curPage + item - 3 >= 1 && curPage + item - 3 <= total"
                              :class="{'turn-active':item == 3}"
                              @click="changePage(curPage + item - 3)">
                              {{curPage + item - 3}}
                          </li>
                          <li v-if="curPage + 2 < total">...</li>
                          <li v-if="curPage + 2 < total" @click="changePage(total)">{{total}}</li>
                          <li v-if="curPage < total" @click="changePage(-1)">下一页</li>
                      </ul>
                  </div>
</template>
<script>
import {mapState,mapActions,mapMutations} from 'vuex'
export default {
    computed:{
        ...mapState({
            total:state => Math.ceil(state.total / 10 ),
            curPage:state => state.curPage,
            curName:state => state.curName
        })
    },
    methods:{
        changePage(dom){
           if(dom == 0){
              if(this.curPage - 1 > 0){
                  this.changeCurPage(this.curPage - 1);
              }
           }else if(dom == -1){
              if(this.curPage + 1 <= this.total ){
                  this.changeCurPage(this.curPage + 1);
              }
           }else{
              this.changeCurPage(dom);
           };
          if(this.curName){
             this.search('searchStudent');
          }else{
             this.getStudentList({url:"findByPage",data:{size:10,page:this.curPage}})
          }
        },
        ...mapMutations(['changeCurPage']),
        ...mapActions(['getStudentList','search'])
    }
}
</script>
