<template>
   
     <div class="content">
                    <body-search></body-search>
                    <body-table></body-table>
                    <turn-page></turn-page>
                </div>
</template>
<script>
import BodySearch from '@/components/app-body/BodySearch.vue'
import BodyTable from '@/components/app-body/BodyTable.vue'
import TurnPage from '@/components/app-body/turnPage.vue'
import {mapState} from 'vuex'
export default {
    components:{
      BodySearch,
      BodyTable,
      TurnPage
    },
    methods:{
    },
    computed:{
        ...mapState(['curPage'])
    },
    created(){
        this.$store.dispatch('getStudentList',{
            url:'findByPage',
            data:{
                size:10,
                page:this.curPage
            }
        })
               
    }
}
</script>
