import Vue from 'vue'
import Vuex from 'vuex'
import methods from './assets/methods/methods'
import { resolve } from 'path';
import { reject } from 'q';
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
     studentList:[],
     curStudent:null,
     isMarking:false,
     curPage:1,
     total:0,
     curName:''
  },
  mutations: {
    changeStudentList(state,{arr}){
        state.studentList = arr;
    },
    // changeCurStudent(state,{obj}){
    //   console.log(obj);
    //    state.curStudent = obj;
    // },
    changeMarking(state,{boolean,obj}){
      state.isMarking = boolean;
      state.curStudent = obj;
    },
    unshirtArr(state,{data}){
      state.studentList.unshift(data);
    },
    changeTotal(state,num){
      state.total = num;
    },
    changeCurPage(state,num){
      state.curPage = num;
    },
    changeCurName(state,name){
      state.curName = name;
    }
  },
  actions: {
     getStudentList(ctx,{url,data}){
       methods.ajax(url,data).then((res) =>{
         console.log(res,'getStudentList');
         const arr = res.data.data.findByPage;
         const total = res.data.data.cont;
         ctx.commit('changeStudentList',{arr});
         ctx.commit('changeTotal',total);
       })
     },
     updateStudent({commit,state},{url,data}){
       return new Promise((resolve,reject)=>{
        methods.ajax(url,data).then((res)=>{
          
          if(res.data.status == "success"){
          
            Object.assign(state.curStudent,data);
            commit('changeMarking',{boolean:false});
            resolve(res.data.msg);
          }else{
            reject(res.data.msg) 
          }
     })
       })
     },
     deleteStudent({commit,state,dispatch},{url,data}){
       const obj = {sNo:data.sNo};
       commit('changeMarking',{obj});
       return new Promise((resolve,reject)=>{
         methods.ajax(url,obj).then((res)=>{
           if(res.data.status == 'success'){
            //  console.log('请求成功了')
            //  const index = state.studentList.findIndex((item,index)=>{
            //       return item.sNo == data.sNo;
            //  });
            //  state.studentList.splice(index,1);
            //这种是直接删除了该对象,但是页面没有请求新的
            dispatch('getStudentList',{url:"findByPage",data:{size:10,page:state.curPage}});
            resolve(res.data.msg);
           }else{
             console.log(res.data);
           }
         })
       })
     },
     add({commit,state,dispatch},{url,data}){
       return new Promise((resolve,reject)=>{
         methods.ajax(url,data).then((res)=>{
          console.log(res.data)
           if(res.data.status == 'success'){
            resolve(res.data.msg);
           }else{
             reject(res.data.msg);
           }
         })
       })
     },
     search({commit,state},url){
       const obj = {sex:-1,page:state.curPage,size:10};
       if(state.curName){
         obj.search = state.curName;
       }
       return new Promise((resolve,reject)=>{
         methods.ajax(url,obj).then((res)=>{
           console.log(res);
           if(res.data.data.cont !== 0){
            const arr = res.data.data.searchList;
            commit('changeTotal',res.data.data.cont);
            commit('changeStudentList',{arr})
            resolve(res.data.msg);
           }else{
             reject('没有找到匹配内容');
           }
         })
       })
     }
  
  }
})
