<template>
    <div class="mark" @click.self="changeMarking({boolean:false})">
             <form action="" class="mark-form">
            <div>
                <label for="name">姓名</label>
                <input type="text" name="name" id="name" ref="name" :value="curStudent.name" @input="changeContent('name',$event.target.value)">
            </div>
                 <div>
                    <label for="sex">性别</label>
                    <input type="radio" name="sex" id="sex" value="0" ref="sex" :checked="curStudent.sex == '男'" @change="changeContent('sex',$event.target.value)">
                    <span>男</span>
                     <input type="radio" name="sex" value="1" :checked="curStudent.sex == '女'" @change="changeContent('sex',$event.target.value)">
                     <span>女</span>
                 </div>
            <div>
                <label for="sNo">学号</label>
                <input type="text" name="sNo" id="sNo" ref="sNo" :value="curStudent.sNo" @input="changeContent('sNo',$event.target.value)">
            </div>
            <div>
                <label for="email">邮箱</label>
                <input type="text" name="email" id="email" ref="email" :value="curStudent.email" @input="changeContent('email',$event.target.value)">
            </div>
            <div>
                <label for="birth">出生年</label>
                <input type="text" name="birth" id="birth" ref="birth" :value="curStudent.birth" @input="changeContent('birth',$event.target.value)">
            </div>
            <div>
                <label for="phone">手机号</label>
                <input type="text" name="phone" id="phone" ref="phone" :value="curStudent.phone" @input="changeContent('phone',$event.target.value)">
            </div>
            <div>
                <label for="address">住址</label>
                <input type="text" name="address" id="address" ref="address" :value="curStudent.address" @input="changeContent('address',$event.target.value)"> 
            </div>
            <div class="submit">
                <input type="button" value="提交" class="btn" id="send" @click="send">
                <input type="button" value="重置" class="btn" @click="reset">
            </div>
             </form>
         </div>
</template>
<script>
import {mapState,mapMutations,mapActions} from 'vuex'
import methods from '../../assets/methods/methods.js'
export default {
    data(){
        return {
            changeUser:{}
        }
    },
    computed:{
        ...mapState(['curStudent'])
    },
    methods:{
        reset(){
            for(let prop in this.$refs){
               if(prop != 'sex'){
                   this.$refs[prop].value = '';
               }else{
                   this.$refs[prop].checked = true;
               }
            }
        },
        changeContent(key,value){
           this.changeUser[key] = value;
        },
        send(){
            var obj = Object.assign({},this.curStudent,this.changeUser);
            console.log(obj);
            this.updateStudent({url:'updateStudent',data:obj})
            .then((res)=>{
                alert(res);
            }).catch((err)=>{
                alert(err);
            })
   
        },
        ...mapMutations(['changeMarking']),
        ...mapActions(['updateStudent'])
    }
}
</script>
<style>


</style>


