<template>

     <div class="input">
                      <form action="">
                          <label for="search">
                            <span class="label">关键字搜索:</span>
                             
                          </label>
                          <input type="text" id="search"  v-model="curValue">
                          <input type="button" value="搜索" class="btn" @click.prevent="mySearch">
                      </form>
                  </div>
                
</template>
<script>
import {mapState,mapActions,mapMutations} from 'vuex'
export default {
    
    data(){
      return {
        curValue:'',
       
      }
    },
    computed:{
        ...mapState(['curName','curPage'])
    },
    methods:{
      mySearch(){
         this.changeCurName(this.curValue);
         if(this.curValue == ''){
           this.changeCurPage(1);
            this.getStudentList({url:'findByPage',data:{size:10,page:this.curPage}})
         }else{
         
         this.search('searchStudent')
         .then((res)=>{
           alert(res);
         }).catch((err)=>{
           alert(err);
         })
         }
      },
      ...mapActions(['search','getStudentList']),
      ...mapMutations(['changeCurName','changeCurPage'])
    }

}
</script>
