<template>
          
             <div class="form">
                <form action="" id="add-form">
                    <div>
                        <label for="name">姓名</label>
                        <input type="text" name="name" id="name" @input="addStudent('name',$event.target.value)" ref="name">
                        <span class="fail">&nbsp;&nbsp;</span>
                    </div>
                         <div>
                            <label for="sex">性别</label>
                            <input type="radio" name="sex" id="sex" checked value="0" @change="addStudent('sex',0)" ref="sex">
                            <span>男</span>
                             <input type="radio" name="sex" value="1" @change="addStudent('sex',1)" :checked="nowStudent.sex == 1">
                             <span>女</span>
                             
                         </div>
                    <div>
                        <label for="sNo">学号</label>
                        <input type="text" name="sNo" id="sNo" @input="addStudent('sNo',$event.target.value)" ref="sNo">
                        <span class="fail">&nbsp;&nbsp;</span>
                      
                    </div>
                    <div>
                        <label for="email">邮箱</label>
                        <input type="text" name="email" id="email" @input="addStudent('email',$event.target.value)" ref="email">
                        <span class="fail">&nbsp;&nbsp;</span>
                    </div>
                    <div>
                        <label for="birth">出生年</label>
                        <input type="text" name="birth" id="birth" @input="addStudent('birth',$event.target.value)" ref="birth">
                        <span class="fail">&nbsp;&nbsp;</span>
                    </div>
                    <div>
                        <label for="phone">手机号</label>
                        <input type="text" name="phone" id="phone" @input="addStudent('phone',$event.target.value)" ref="phone">
                        <span class="fail">&nbsp;&nbsp;</span>
                    </div>
                    <div>
                        <label for="address">住址</label>
                        <input type="text" name="address" id="address" @input="addStudent('address',$event.target.value)" ref="address">
                        <span class="fail">&nbsp;&nbsp;</span>
                    </div>
                    <div class="submit">
                        <input type="button" value="提交" class="btn" id="add-student" @click.prevent="sendFrom('addStudent')">
                        <input type="button" value="重置" class="btn" @click.prevent="reset">
                    </div>
                     </form>
             </div>
           
</template>
<script>
import {mapActions} from 'vuex'
export default {
    data(){
        return {
            nowStudent:{sex:0},
        }
    },
    methods:{
        addStudent(key,value){
            this.nowStudent[key] = value;
        },
        reset(){
            for(let prop in this.$refs){
               if(prop != 'sex'){
                   this.$refs[prop].value = '';
               }
            }
        },
        sendFrom(url){
            console.log(this.nowStudent);
           this.add({url,data:this.nowStudent})
           .then((res)=>{
               alert(res);
              this.$router.push({name:'body'})
           }).catch((err)=>{
               alert(err);
           })
        },
        ...mapActions(['add'])
    }
}
</script>
<style>

</style>


