<template>
  <div>
    <div class="list">
      <table border="0" cellspacing="0">
        <thead>
          <tr colspan="8">
            <th>学号</th>
            <th>姓名</th>
            <th>性别</th>
            <th>邮箱</th>
            <th>年龄</th>
            <th>手机号</th>
            <th>住址</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody id="add-tb">
          <tr v-for="(item,index) in studentList" :key="index"> 
            <td>{{item.sNo}}</td>
            <td>{{item.name}}</td>
            <td>{{item.sex}}</td>
            <td>{{item.email}}</td>
            <td>{{item.birth}}</td>
            <td>{{item.phone}}</td>
            <td>{{item.address}}</td>
            <td>
              <button class="red" :data-index="index" @click="changeMarking({boolean:true,obj:item})">编辑</button>
              <button class="green" :data-index="index" @click="myDelete('delBySno',item)">删除</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import {mapState,mapMutations,mapActions} from 'vuex'
export default {
   computed:{
       ...mapState(['studentList'])
   },
   methods:{
        myDelete(url,data){
          this.deleteStudent({url,data})
          .then((res)=>{
              alert(res);
          })
       },
       ...mapMutations(['changeMarking']),
       ...mapActions(['deleteStudent']),
      
   },

};
</script>
