import Vue from 'vue'
import Router from 'vue-router'
import AppBody from './components/app-body/Body.vue'
import AddFrom from './components/from/AddFrom.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes:[
    
    {
       path:'/body',
       name:'body',
       component:AppBody,
        alias:'/'
    },
    {
      path:'/add',
      name:'add',
      component:AddFrom
    }
  ]
  
})
